'use client';

import { Fragment } from 'react';
import { useParams, useSelectedLayoutSegment } from 'next/navigation';
import {
  NavMenu,
  NavMenuItem,
  NavMenuSubMenu,
  NavMenuSubMenuItem,
} from './nav-menu';

interface SideNavMenuProps {
  basePath: string;
  title: string;
  menuItems: {
    segment: string | null;
    path: string;
    label: string;
    subItems?: {
      segment: string;
      path: string;
      label: string;
    }[];
  }[];
}

export function SideNavMenu({ title, menuItems, basePath }: SideNavMenuProps) {
  const segment = useSelectedLayoutSegment();
  const { id } = useParams() as { id: string };

  return (
    <div className="hidden h-screen basis-72 overflow-scroll border-r-[1px] border-subtle lg:block">
      <p className="whitespace-nowrap border-b-xs border-subtle p-5 text-sm text-smooth">
        {title}
      </p>

      <NavMenu>
        {menuItems.map((item) => (
          <Fragment key={item.label}>
            <NavMenuItem
              active={segment === item.segment}
              href={`${basePath}${id}${item.path}`}
            >
              {item.label}
            </NavMenuItem>

            {item.subItems ? (
              <NavMenuSubMenu>
                {item.subItems.map((subItem) => (
                  <NavMenuSubMenuItem
                    active={segment === item.segment}
                    href={`${basePath}${id}${item.path}`}
                    key={subItem.label}
                  >
                    {subItem.label}
                  </NavMenuSubMenuItem>
                ))}
              </NavMenuSubMenu>
            ) : null}
          </Fragment>
        ))}
      </NavMenu>
    </div>
  );
}
