import type { Url } from 'next/dist/shared/lib/router/router';
import Link from 'next/link';
import type { PropsWithChildren } from 'react';
import { cn } from '@axiom-ui/react/lib';

export function NavMenu({ children }: PropsWithChildren) {
  return (
    <nav className="mb-5">
      <ul>{children}</ul>
    </nav>
  );
}

export function NavMenuSubMenu({ children }: PropsWithChildren) {
  return <ul>{children}</ul>;
}

interface NavMenuSubMenuItemProps {
  children: string;
  active: boolean;
  href: Url;
}

export function NavMenuSubMenuItem({
  children,
  active,
  href,
}: NavMenuSubMenuItemProps) {
  return (
    <li
      className={cn(
        'border-b-xs border-subtle bg-base-2 px-8 py-1',
        active &&
          'border-l-[3px] border-l-activated text-interactive-accent-default',
      )}
    >
      <Link href={href}>{children}</Link>
    </li>
  );
}

interface NavMenuItemProps {
  children: string;
  active?: boolean;
  href: Url;
}

export function NavMenuItem({ children, active, href }: NavMenuItemProps) {
  return (
    <li
      className={cn(
        `
      border-b-xs
      border-subtle
      px-5
      py-4
      text-xxs
      text-interactive-accent-default
      `,
        active && 'border-l-[4px] border-l-activated bg-base-2',
      )}
    >
      <Link href={href}>{children}</Link>
    </li>
  );
}
