'use client';

import { useSelectedLayoutSegment } from 'next/navigation';

interface PageTitleProps {
  menuItems: {
    segment: string | null;
    path: string;
    label: string;
    subItems?: {
      segment: string;
      path: string;
      label: string;
    }[];
  }[];
}

export function PageTitle({ menuItems }: PageTitleProps) {
  const segment = useSelectedLayoutSegment();

  return (
    <h1 className="px-5 pb-4 pt-6 text-md text-smooth">
      {menuItems.flat().find((item) => item.segment === segment)?.label}
    </h1>
  );
}
